  background-color: black;
  height: 200px;
  width: 30px;
  position: absolute;
  top: 0;
  z-index: 100;
}

.octave__key--should-be-pressed {
  background-color: blue;
}

.octave__key--on {
  background-color: orange;
}

.octave__key--black:nth-child(2) {
  left: 35px;
}

.octave__key--black:nth-child(4) {
  left: 85px;
}

.octave__key--black:nth-child(7) {
  left: 185px;
}

.octave__key--black:nth-child(9) {
  left: 235px;
}

.octave__key--black:nth-child(11) {
  left: calc(285px - var(--offset, 0px));
}

.octave-key:not(.octave__key--black) {
